import { Component, OnInit } from '@angular/core';
import { PegawaiDetailService } from '../../../service/pegawai-detail.service';
import { GaUniformService, GaUniformMasterxService, DirektoriPegawaiMasterService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { NzNotificationService } from 'ng-zorro-antd';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router, ActivatedRoute } from '@angular/router';
import { Uniform, Pegawai_master, Uniform_masterx } from 'src/app/sdk/hr/models';
import { FormControl } from '@angular/forms';

@Component({
  selector: 'app-edit',
  templateUrl: './edit.component.html',
  styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {

    constructor(
        private gaUniformService: GaUniformService,
        private gaUniformMasterxService: GaUniformMasterxService,
        private direktoriPegawaiMasterService: DirektoriPegawaiMasterService,
        private pegawaiDetailService: PegawaiDetailService,
        private spinner: NgxSpinnerService,
        private notification: NzNotificationService,
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
    ) {}

    dataUniform: Uniform = {}

    listPegawai: Pegawai_master[] = []
    listUniform: Uniform_masterx[] = []

    id_pegawai: string = ''
    id_uniform: string = ''


    ngOnInit() {
        this.id_uniform = this.activatedRoute.snapshot.paramMap.get('id')
        this.pegawaiDetailService.getIdPegawai().subscribe(data => {
            this.id_pegawai = data
            console.log('id pegawai paja tu ' + this.id_pegawai)
        })
        this.getPreeData()
        this.form()
        this.getData()
    }
    getPreeData() {
        this.direktoriPegawaiMasterService.getDirektoriPegawaiMasters().subscribe(data => {
            this.listPegawai = data.data
        })

        this.gaUniformMasterxService.getGaUniformMasterxes().subscribe(data => {
            this.listUniform = data.data
        })
    }

    getData() {
        this.gaUniformService.getGaUniformsId(this.id_uniform).subscribe(
            data=> {
                this.dataForm.setValue(data.data)
            }
        )
    }

    dataForm: FormGroupTypeSafe<Uniform>
    form() {
        this.dataForm = this.fb.group<Uniform>({
            id_pegawai: new FormControl(this.id_pegawai),
            id_uniform_master: new FormControl(''),
            ukuran: new FormControl(''),
            jumlah: new FormControl(''),
            tanggal_request: new FormControl(''),
            id_data_flow: new FormControl(''),
            id_data_flow_detail: new FormControl(''),
            id_uniform: new FormControl(''),
            id_user: new FormControl(''),
            created_at: new FormControl(''),
            updated_at: new FormControl(''),
            nomor: new FormControl(''),
            status: new FormControl(''),
            posisi: new FormControl(''),
            id_dokumen: new FormControl(''),
        })
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid')
            this.spinner.hide()
            return
        }

        this.dataForm.patchValue({id_pegawai: this.id_pegawai})

        this.dataUniform = this.dataForm.value
        this.gaUniformService.putGaUniformsId({id: this.id_uniform, body: this.dataUniform}).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            },
            err => {
                this.pesan.pesanWarningForm('Gagal di simpan')
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            },
        )
    }

    goToBack() {
        this.router.navigate(['/profile/uniform/list'])
    }


}
